.LoadingScreen {
    width: 100%;
    height: 100vh;
}

.LoadingScreen {
    display: flex;
    justify-content: center;
    align-items: center;
}

@media only screen and (max-width: 768px) {
    .loader {
        width: 10%;
        height: 10%;
        transform: translate(-50%, -50%);
    }
}
